@import "../mixins.scss";

.menu {
    cursor: pointer;
}
nav {
    display: block;
    div.info {
        display: block;
    }
    div.set {
        button {
            cursor: pointer;
        }
    }
}

//piano

.menu.piano {
    @include t(60px,60px);
    @include xy-tl(0,50%);
    transform: translateX(-30px);
    background: none;
    border: none;
    div {
    @include t(100%,3px);
    transform: translateY(-7px);
    background: $piano1;
    transition: 0.5s;
    &::after {
        @include pseudo(100%,3px);
        transition: 0.5s;
        transform: translateY(14px);
            background: $piano1;
        }
    }
    &.active {
        div {
            transform: rotate(30deg);
            &::after {
                transform: rotate(300deg);
            }
        }
    }
}

    
nav.piano {
    @include t(100%,100vh);
    @include xy-tl(0,0);
    padding-top: 60px;
    z-index: 50;
    background: $piano2;
    color: $piano1;
    @include pianofont;
    transition: 1s;
    border-bottom: 5px solid $piano1;
    &.active {
        transform: translateY(0%);
    }
    &.no-active {
        transform: translateY(-100%);
    }
    div.info {
        h3 {
            text-align: center;
            padding: 30px;
            font-size: 2em;
            border-bottom: 5px solid $piano1;
            width: 80%;
            margin-left: 10%;
        }
        p {
            padding: 30px 10% 30px 10%;
            text-align: center;
        }
    }
    div.set {
        @include centrar(v);
        button {
            margin: 10px;
            border-style: none;
            background: none;
            font-size: 4em;
            padding: 10px;
            &:hover {
                transform: scale(1.2);
            }
        }
    }
}

//toxic

.menu.toxic {
    position: fixed;
    background: #fff;
    border-radius: 10px 10px 0 0;
    border-style: none;
    div {
        background: #333;
        position: relative;
        border-radius: 5px;
        transition: 0.5s;
        &::after {
            @include pseudo(100%,100%);
            transform: translateY(12px);
            background: #333;
            border-radius: 5px;
            transition: 0.5s;
        }
        &::before {
            @include pseudo(100%,100%);
            transform: translateY(-12px);
            background: #333;
            border-radius: 5px;
            transition: 0.5s;
        }
    }
    &.active {
        div {
            background: transparent;
            &::after {
                transform: rotate(20deg);
            }
            &::before {
                transform: rotate(340deg);
            }   
        }
    }
}

nav.toxic {
    position: fixed;
    z-index: 80;
    background: #fff;
    box-shadow: 0 0 5px $toxic2;
    @include toxicfont;
    div.info {
        display: block;
        padding: 5%;
        h3,p {
            text-align: center;
        }
        h3 {
            padding: 30px;
            font-size: 2em;
        }
    }
}

@media (orientation:portrait){
    .menu.toxic {
        @include t(50px,50px);
        border-radius: 0 10px 10px 0;
        box-shadow: 0 0 5px $toxic2;
        top: 15px;
        left: 0;
        div {
            @include t(40px,5px);
            margin-left: 5px;
        }
    }
    nav.toxic {
        @include t(100%,100vh);
        &.active {
            transform: translateX(0%);
        }
        &.no-active {
            transform: translateX(-100%);
        }
        div.info {
            @include toxicfont;
        }
    }
}
@media (orientation:landscape){
    .menu.toxic {
        @include t(70px,70px);
        border-radius: 10px 10px 0 0;
        border-bottom: 1px solid #333;
        top: 35px;
        left: 35px;
        div {
            margin-left: 10px;
            @include t(50px,7px);
        }
    }
    nav.toxic {
        width: 70px;
        top: 35px;
        left: 35px;
        bottom: 35px;
        padding-top: 70px;
        border-radius: 10px;
        div.info {
            position: fixed;
            left: 140px;
            top: 35px;
            bottom: 35px;
            width: 40vw;
            background: rgba(150,150,150,0.7);
            backdrop-filter: blur(5px);
            border-radius: 10px;
        }
        &.active {
            div.info {
                transform: translateY(0);
            }
        }
        &.no-active {
            div.info {
                transform: translateY(-100vh);
            }
        }
        div.set {
            @include t(100%,100%);
            position: relative;
            @include centrar(v);
            button {
                @include t(60px,60px);
                @include centrar(vh);
                position: absolute;
                border-style: none;
                border-radius: 10px;
                font-size: 1.5em;
                color: #333;
                background: none;
                &:nth-child(1) {
                    top: 15%;
                }
                &:nth-child(2) {
                    top: 40%;
                }
                &:nth-child(3) {
                    top: 65%;
                }
                &:hover {
                    background: $toxic2;
                    box-shadow: 0 0 15px $toxic2;
                    animation: linear color 3s infinite;
                    transform: translateX(50%);
                }
            }
        }
    }
}

// neomorfismo 

#menu.neomorfismo {
    position: absolute;
    top: 15px;
    &::before {
        content: '';
        @include t(90%,5px);
        position: absolute;
        background: #888;
        top: 45%;
        left: 5%;
        z-index: -1;
    }
    button {
        @include t(60px,50px);
        background: $neomorfismo1;
        border-top: 5px solid #e9e9e9;
        border-left: 10px solid #e9e9e9;
        border-right: 10px solid #bbb;
        border-bottom: 2px solid #bbb;
        transition: 0.5s;
        box-shadow: 3px 3px 3px rgba(100,100,100,0.2),
        6px 6px 3px rgba(100,100,100,0.2),
        9px 9px 3px rgba(100,100,100,0.2),
        inset 1px 1px 1px #e3e3e3,
        inset -1px -1px 1px #c9c9c9;
        @include centrar(h);
        div {
            @include t(70%,2px);
            left: 15%;
            background: $neomorfismo3;
            position: relative;
            &::after {
                @include pseudo(100%,100%);
                transform: translateY(8px);
                background: $neomorfismo3;
                transition: 0.5s;
            }
            &::before {
                @include pseudo(100%,100%);
                transform: translateY(-8px);
                background: $neomorfismo3;
                transition: 0.5s;
            }
        }
    }
    &.active {
        button {
            transform: translateX(120%);
            div {
                background: transparent;
                &::after {
                    transform: rotate(20deg);
                }
                &::before {
                    transform: rotate(340deg);
                }   
            }
        }
    }
}
nav.neomorfismo {
    @include t(100%,100vh);
    @include xy-tl(0,0);
    z-index: 50;
    background: $neomorfismo1;
    color: $neomorfismo3;
    @include pianofont;
    transition: 1s;
    &.active {
        transform: translateY(0%);
    }
    &.no-active {
        transform: translateY(-100%);
    }
    div.info {
        background: $neomorfismo2;
        border-radius: 10px;
        @include neomorfismo-bs2;
        h3 {
            text-align: center;
            padding: 30px;
            font-size: 2em;
            width: 80%;
            margin-left: 10%;
        }
        p {
            padding: 30px 10% 30px 10%;
            text-align: center;
        }
    }
    div.set {
        margin-top: 20px;
        @include centrar(v);
        button {
            margin: 10px;
            @include t(60px,60px);
            @include centrar(vh);
            border-style: none;
            border-radius: 50%;
            font-size: 20px;
            color: #333;
            background: $neomorfismo1;
            @include neomorfismo-bs;
            &:hover  {
                @include neomorfismo-bsi;
            }
        }
    }
}
@media (orientation:landscape){
    #menu.neomorfismo {
        width: 10%;
        right: 10%;
    }
    nav.neomorfismo {
        padding: 110px 10% 0 10%;
    }
}
@media (orientation:portrait){
    #menu.neomorfismo {
        width: 30%;
        right: 5%;
    }
    nav.neomorfismo {
        padding: 110px 5% 0 5%;
    }
}

